"use client";
import { useParams } from "next/navigation";
import { useEffect, useState } from "react";
import { getServiceInfo } from "@/lib/client/fetchData";
import ServiceClient from "./ServiceClient";

const ServiceClientWrapper = () => {
  const params = useParams();
  const [serviceInfo, setServiceInfo] = useState(null);

  useEffect(() => {
    if (params?.service) {
      const serviceKey = decodeURIComponent(params.service);
      setServiceInfo(getServiceInfo(serviceKey));
    }
  }, [params?.service]);

  if (!serviceInfo) {
    return <div>Service ikke fundet</div>;
  }

  return (
    <ServiceClient serviceInfo={serviceInfo} serviceKey={params.service} />
  );
};

export default ServiceClientWrapper;
